.container {
  width: 100%;
  background-color: #fff;
  height: 100%;
}
.link {
  color: #2DAC91;
  font-size: 14px;
  cursor: pointer;
  display: inline-block;
}
.link .anticon {
  margin: 0px 10px 0px 30px;
}
.link:hover {
  color: #f00;
}
.disableStyle {
  color: #cccccc;
}
.label {
  font-size: 14px;
  display: inline-block;
}
.arrow {
  margin-right: 10px;
  color: #2DAC91;
  cursor: pointer;
}
.ant-tag {
  margin: 0px 8px;
  cursor: pointer;
}
.navHeader {
  height: 64px;
  background-color: var(--sider-bg);
  color: #000;
  padding: 0px 20px;
  line-height: 64px;
  box-shadow: 1px 7px 4px rgba(0, 0, 0, 0.1);
  margin: 0px 0px 0px 1px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.navHeader .toggle-btn {
  cursor: pointer;
}
.navHeader .toggle-btn .anticon {
  font-size: 26px;
  color: #cccccc;
}
.navHeader .centerText {
  color: var(--main-f-color);
}
.navHeader .header-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.navHeader .header-info .ant-dropdown-menu-item,
.navHeader .header-info .ant-dropdown-menu-submenu-title {
  padding: 5px 20px;
}
.navHeader .header-info .welcome {
  font-size: 14px;
  margin-right: 10px;
  cursor: default;
  color: var(--main-f-color);
}
.navHeader .header-info .welcome span {
  color: #2DAC91;
  display: inline-block;
  margin-left: 10px;
}
.navHeader .header-info .welcome p {
  display: inline-block;
  margin-right: 10px;
}
.navHeader .header-info .ant-dropdown-link {
  color: var(--main-f-color);
  cursor: pointer;
}
.navHeader .ant-switch {
  background-color: var(--switch-theme);
}
