.container {
  width: 100%;
  background-color: #fff;
  height: 100%;
}
.link {
  color: #2DAC91;
  font-size: 14px;
  cursor: pointer;
  display: inline-block;
}
.link .anticon {
  margin: 0px 10px 0px 30px;
}
.link:hover {
  color: #f00;
}
.disableStyle {
  color: #cccccc;
}
.label {
  font-size: 14px;
  display: inline-block;
}
.arrow {
  margin-right: 10px;
  color: #2DAC91;
  cursor: pointer;
}
.ant-tag {
  margin: 0px 8px;
  cursor: pointer;
}
.product-container {
  padding: 20px;
}
.product-index {
  width: 100%;
  min-height: 100%;
}
.product-detail .ant-list-item {
  justify-content: normal;
}
.product-detail .left-tit {
  font-weight: 700;
  font-size: 18px;
  letter-spacing: 2px;
  display: inline-block;
  width: 100px;
  white-space: nowrap;
}
.product-detail .right-con {
  text-align: left;
}
.product-detail .right-con .picker {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  margin: 0px 10px 10px 0px;
}
