.container {
  width: 100%;
  background-color: #fff;
  height: 100%;
}
.link {
  color: #2DAC91;
  font-size: 14px;
  cursor: pointer;
  display: inline-block;
}
.link .anticon {
  margin: 0px 10px 0px 30px;
}
.link:hover {
  color: #f00;
}
.disableStyle {
  color: #cccccc;
}
.label {
  font-size: 14px;
  display: inline-block;
}
.arrow {
  margin-right: 10px;
  color: #2DAC91;
  cursor: pointer;
}
.ant-tag {
  margin: 0px 8px;
  cursor: pointer;
}
.ant-layout-content {
  padding: 20px;
  background-color: var(--content-bg);
}
.ant-layout-content .main-container {
  background-color: #fff;
  height: 100%;
  border-radius: 10px;
  padding: 20px;
}
.ant-layout-content .guide-desc {
  font-size: 14px;
  color: #ccc;
  margin-bottom: 10px;
}
.ant-layout-content .guide-desc i {
  font-size: 24px;
  color: #FFC107;
}
.ant-layout-content .collapseTip {
  background-color: #f00;
}
.ant-layout-content .magnifier .box {
  width: 350px;
  height: 350px;
  margin: 100px;
  border: 1px solid #ccc;
  position: relative;
}
.ant-layout-content .magnifier .box .big {
  width: 400px;
  height: 400px;
  position: absolute;
  top: 0;
  left: 360px;
  border: 1px solid #ccc;
  overflow: hidden;
  display: none;
}
.ant-layout-content .magnifier .box .big img {
  position: absolute;
  width: 800px;
  max-width: 800px;
}
.ant-layout-content .magnifier .box .mask {
  width: 175px;
  height: 175px;
  background: rgba(255, 255, 0, 0.4);
  position: absolute;
  top: 0px;
  left: 0px;
  cursor: move;
  display: none;
}
.ant-layout-content .magnifier .box .small {
  position: relative;
}
.ant-layout-footer {
  background: var(--fotter-bg);
  color: var(--sider-bar-item) !important;
  font-weight: 700;
}
.ant-card-bordered {
  border-width: 0px;
}
