.container {
  width: 100%;
  background-color: #fff;
  height: 100%;
}
.link {
  color: #2DAC91;
  font-size: 14px;
  cursor: pointer;
  display: inline-block;
}
.link .anticon {
  margin: 0px 10px 0px 30px;
}
.link:hover {
  color: #f00;
}
.disableStyle {
  color: #cccccc;
}
.label {
  font-size: 14px;
  display: inline-block;
}
.arrow {
  margin-right: 10px;
  color: #2DAC91;
  cursor: pointer;
}
.ant-tag {
  margin: 0px 8px;
  cursor: pointer;
}
.category-container {
  height: 100%;
}
.category-container .ant-card {
  height: 100%;
  overflow: hidden;
}
.edit-category .ant-form-item {
  margin-bottom: 10px;
}
.ant-modal-body {
  padding: 15px 24px 24px;
}
