.login {
  width: 100%;
  height: 100%;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login .loginForm {
  width: 500px;
  height: 400px;
  background-color: #fff;
}
.login .loginForm h2 {
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  margin: 40px 0px;
}
.login .loginForm .login-form {
  margin: 30px 40px;
}
.login .loginForm .login-form .form-input {
  height: 40px;
}
.login .loginForm .login-form .login-form-button {
  width: 100%;
  height: 40px;
  margin-top: 30px;
}
