ul,
ol {
  list-style: none;
}
.slider-container {
  width: 500px;
  height: 200px;
  border: 1px solid #ccc;
  margin: 100px auto;
  position: relative;
}
.slider-container .slider {
  width: 500px;
  height: 200px;
  overflow: hidden;
  position: relative;
}
.slider-container .slider ul {
  position: absolute;
  left: 0;
  top: 0px;
  width: 3000px;
}
.slider-container .slider ul li {
  width: 500px;
  height: 200px;
  overflow: hidden;
  float: left;
}
.slider-container .slider ol {
  position: absolute;
  right: 10px;
  bottom: 10px;
  line-height: 20px;
  text-align: center;
}
.slider-container .slider ol li {
  float: left;
  width: 20px;
  height: 20px;
  background: #fff;
  border: 1px solid #ccc;
  margin-left: 10px;
  cursor: pointer;
}
.slider-container .slider ol .current {
  background: yellow;
}
.slider-container .arrow {
  display: none;
  z-index: 1000;
  width: 100%;
}
.slider-container .arrow span {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  margin-top: -20px;
  background: #000;
  cursor: pointer;
  line-height: 40px;
  text-align: center;
  font-weight: bold;
  font-family: '黑体';
  font-size: 30px;
  color: #fff;
  opacity: 0.3;
  border: 1px solid #fff;
}
.slider-container .arrow span.rightArr {
  right: 5px;
}
