.container {
  width: 100%;
  background-color: #fff;
  height: 100%;
}
.link {
  color: #2DAC91;
  font-size: 14px;
  cursor: pointer;
  display: inline-block;
}
.link .anticon {
  margin: 0px 10px 0px 30px;
}
.link:hover {
  color: #f00;
}
.disableStyle {
  color: #cccccc;
}
.label {
  font-size: 14px;
  display: inline-block;
}
.arrow {
  margin-right: 10px;
  color: #2DAC91;
  cursor: pointer;
}
.ant-tag {
  margin: 0px 8px;
  cursor: pointer;
}
.lang {
  margin-left: 10px;
}
.lang span {
  cursor: pointer;
  font-weight: 600;
  padding: 0px 5px;
}
.lang .checked {
  color: #2DAC91;
}
